package wizard.files.app

import wizard.ProjectFile
import wizard.ProjectInfo

class Podfile : ProjectFile {
    override val path = "iosApp/Podfile"
    override val content = """
        target 'iosApp' do
          use_frameworks!
          platform :ios, '16.2'
          pod 'composeApp', :path => '../composeApp'
        end
        
    """.trimIndent()
}

